body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
  }
  
  #app {
    display: flex;
    flex-direction: column;
    height: 100vh;
  }
  
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #333;
    color: white;
  }
  
  .header > div {
    flex: 1;
    text-align: center;
  }
  
  .main-layout {
    display: flex;
    flex: 1;
    overflow: hidden;
  }
  
